
/**
 * jcw2d
 *
 * @class JC.InteractionData
 * @constructor
 */
function InteractionData(){
    /**
     * canvas
     *
     * @property global
     * @type Object
     */
    this.global = {x:-100000,y:-100000};

    /**
     * 
     *
     * @property target
     * @type JC.Container
     */
    this.target = null;

    /**
     * 
     *
     * @property originalEvent
     * @type Event
     */
    this.originalEvent = null;

    /**
     * canvas
     *
     * @property cancleBubble
     * @type Boolean
     */
    this.cancleBubble = false;

    /**
     * canvas
     *
     * @property ratio
     * @type Number
     */
    this.ratio = 1;

    /**
     * 
     *
     * @property type
     * @type String
     */
    this.type = '';
}
JC.InteractionData = InteractionData;



/**
 * jcw2d
 *
 * @class JC.Eventer
 * @constructor Eventer
 * @memberof JC
 */
function Eventer(){
    this.touchstarted = false;
    this.mouseDowned = false;
    this.listeners = {};
}
/**
 * 
 *
 * @param type {String} 
 * @param fn {Function} 
 * @private
 */
Eventer.prototype.on = function(type,fn){
    this.listeners[type] = this.listeners[type]||[];
    this.listeners[type].push(fn);
};
/**
 * 
 *
 * @param type {String} 
 * @param fn {Function} 
 * @private
 */
Eventer.prototype.off = function(type,fn){
    if (this.listeners[type]) {
        var i = this.listeners[type].length;
        if(fn){
            while (i--) {
                if (cbs[type][i] === fn) {
                    cbs[type].splice(i, 1);
                }
            }
        }else{
            cbs[type].length = 0;
        }
    }
};
/**
 * 
 *
 * @param ev {JC.InteractionData} 
 * @private
 */
Eventer.prototype.emit = function(ev){
    if ( this.listeners === undefined ) return;
    var ears = this.listeners;
    var cbs = ears[ ev.type ];
    if ( cbs !== undefined ) {
        var length = cbs.length;
        var i;
        for ( i = 0; i < length; i ++ ) {
            cbs[ i ].call( this, ev );
        }
    }
};
JC.Eventer = Eventer;