    JC.TWEEN = {
        easeBoth: function(t, b, c, d) { //
            if ((t /= d / 2) < 1) {
                return c / 2 * t * t + b;
            }
            return -c / 2 * ((--t) * (t - 2) - 1) + b;
        },
        extend: function(opts) {
            if (!opts) return;
            for (var key in opts) {
                if (key !== 'extend' && opts[key]) this[key] = opts[key];
            }
        }
    };
    

    
